import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import * as d3 from "d3";

const GRPAH_CONSTANTS= require('./variables')


class Node extends Component {
    
        componentDidMount() {
          this.d3Node = d3.select(ReactDOM.findDOMNode(this))
            .datum(this.props.data)
            .call(GRPAH_CONSTANTS.FORCE.enterNode)
        }
    
        componentDidUpdate() {
          this.d3Node.datum(this.props.data)
            .call(GRPAH_CONSTANTS.FORCE.updateNode)
        }

        childNodeClicked(id,name,type,isParent){
           // alert("node clicked "+data)
          //  console.log(name,type,isParent);
           switch(type){
             case 0:
            //  console.log("Type 0 1st click show");
                if(id !== "invoice" && !isNaN(id)){
                  this.props.nodeClicked(id,name,1,isParent,0)
                }
                break;
             case 1:
                // console.log("Type 1 2nd click hide");
                if(id !== "invoice" && !isNaN(id)){
                  this.props.nodeClicked(id,name,2,isParent,1)
                }
                break;

             case 2:
                // console.log("Type 2 3rd click show again");
                this.props.nodeClicked(id,name,1,isParent,0)
                break;

             default:
               break;
           }
            // this.props.nodeClicked(id,name)
        }

        render() {
            // console.log(this.props.data);
          return (
            this.props.data.type === 2 && !this.props.data.isParent?
                null
                :
                <g className='node' onClick={()=>this.childNodeClicked(this.props.data.id,this.props.data.name,this.props.data.type,this.props.data.isParent)}>
                      <circle fill={"purple"}               
                      />
                      <text>{this.props.data.name}</text>
                </g>
          );
        }
    }

    export default Node;
    