import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import * as d3 from "d3";

const APP_CONSTANTS = require('./variables')

class Node extends Component {

  componentDidMount() {
    this.d3Node = d3.select(ReactDOM.findDOMNode(this))
      .datum(this.props.data)
      .call(APP_CONSTANTS.FORCE.enterNode)
  }

  componentDidUpdate() {
    console.log("NODES componentDidUpdate")

    this.d3Node.datum(this.props.data)
      .call(APP_CONSTANTS.FORCE.updateNode)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.data !== undefined &&
      this.props.data !== nextProps.data) {
      this.d3Node.datum(this.props.data)
        .call(APP_CONSTANTS.FORCE.updateNode)
    }
  }

  childNodeClicked(id, name) {
    this.props.nodeClicked(id, name)
  }

  render() {
    return (
      <g className='node'
        onClick={() => this.childNodeClicked(this.props.data.id, this.props.data.name)}
       >
        <circle fill={"purple"}
          r={this.props.size}
        />
        <text>{this.props.data.name}</text>
      </g>

      // <g className='node' key={ this.props.data.key} transform={this.props.transform}
      // onClick={() => this.childNodeClicked(this.props.data.id, this.props.data.name)}>
      //   <circle r={ this.props.data.size} />
      //   <text x={ this.props.data.size + 5} dy='.35em'>{this.props.data.name}</text>
      // </g>
    );
  }
}

export default Node;
