import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import * as d3 from "d3";
const APP_CONSTANTS = require('./variables')
class Link extends Component {

    componentDidMount() {
        this.d3Link = d3.select(ReactDOM.findDOMNode(this))
            .datum(this.props.data)
            .call(APP_CONSTANTS.FORCE.enterLink);
    }

    componentDidUpdate() {
        console.log("LINKS componentDidUpdate")
        
        this.d3Link.datum(this.props.data)
            .call(APP_CONSTANTS.FORCE.updateLink);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.data !== undefined &&
          this.props.data !== nextProps.data) {
            this.d3Link.datum(this.props.data)
            .call(APP_CONSTANTS.FORCE.updateLink);
        }
      }
    

    render() {
        return (
             <line className='link' />
            // <line className='link' key={this.props.data.key} strokeWidth={this.props.data.size}
            // x1={this.props.data.source.x} x2={this.props.data.target.x} y1={this.props.data.source.y} y2={this.props.data.target.y} />
        );
    }
}

export default Link;
