import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Tabs, Row, Table, Collapse } from 'antd';
import './index.scss';
import ReactJson from 'react-json-view'
import { pathConstants } from 'constants/path-constants';
import * as Utils from 'utils/util';
import moment from "moment";
import * as Sockets from '../../sockets/socket';
import GraphContainer from 'containers/knowledgeGraph';
import GraphContainerUnCompressed from 'containers/KnowledgeGraphUnCompressed';



// Actions
import { default as ReceiptsActions } from "store/receipts/actions";
// 
import BreadcrumbComponent from 'components/breadcrumb';
import Loader from 'components/loader';
// constants
// const { getUserAction, getReceiptData } = ReceiptsActions;
const { getReceiptData } = ReceiptsActions;
const APP_CONSTANTS = require('constants/app-constants');
// const NETWORK_CONSTANT = require('constants/api-constants');
const TabPane = Tabs.TabPane;
const Panel = Collapse.Panel;
// const Sockets = require('../../sockets/socket');

// var data;
// var endpoint;
var receiptdata = [];
var receiptInfoData = [];

class Receipts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            showHeader: false,
            content: [],
            step: this.props.step,
            status: this.props.status,
            receiptId: '',
            receiptDetailData: {}
        }
        this.generateData = this.generateData.bind(this);
    }

    componentDidMount() {
        debugger;
        var connection;
        var self = this;
        var rID = this.props.location.state.receiptId;
        //rID = APP_CONSTANTS.RECEIPT_ID;
        this.setState({ loading: true });
        this.props.getReceiptData(rID);
        this.setState({ receiptId: rID })

        //ON Socket Data Received
        // console.log('socket'+Sockets.socketConnection)
        if (Sockets.socketConnection !== undefined) {
            connection = Sockets.socketConnection;
        } else {
            connection = Sockets.connectSocket();
        }
        connection.on("receiptTransaction/" + rID, function (data) {
            console.log("Receipts SOCKET DATA RECEIVED for receipt" + JSON.stringify(data))
            self.fetchWebSocketData(data);
        });

    }

    fetchWebSocketData(data) {
        // console.log('SOCKET DATA FOR RECEIPT'+data,data.res.obj);
        // this.generateData(data.res.obj);

        // var temptransactionArray =[];
        var transactionArray = [];
        // temptransactionArray.push(data.res.obj);

        var i = Utils.getIndex(data.res.obj.step);
        var exisitingContent = this.state.content;
        exisitingContent[i] = data.res.obj;


        transactionArray = Utils.getTransactionArray(exisitingContent);
        receiptdata = [];
        receiptInfoData = [];
        for (var index = 0; index < transactionArray.length; index++) {
            receiptdata.push({
                // 'key':index,
                'headerkey': 'id',
                'value': transactionArray[index].id,
            },
                {
                    // 'key':index,
                    'headerkey': 'receiptId',
                    'value': transactionArray[index].receiptId
                },
                {
                    // 'key':index,
                    'headerkey': 'Tx#',
                    'value': transactionArray[index].transactionId
                },
                // {
                //     // 'key':index,
                //     'headerkey': 'Step',
                //     'value': transactionArray[index].step
                // },
                {
                    // 'key':index,
                    'headerkey': 'Status',
                    'value': transactionArray[index].status
                },
                {
                    // 'key':index,
                    'headerkey': 'Date',
                    'value': transactionArray[index].transDate === undefined ? " - "
                        : moment(transactionArray[index].transDate).format('DD/MM/YYYY')
                },
            )
            receiptInfoData.push(receiptdata);
            receiptdata = [];
        }
        var exisitingData = this.state;
        console.log("JSON LD " + JSON.stringify(exisitingData.receiptDetailData.jsonLd.minimumPaymentDue))
        this.setState({
            step: data.res.obj.step,
            loading: false,
            content: transactionArray,
            status: data.res.obj.status,
            receiptDetailData: exisitingData.receiptDetailData,
        })
        this.setState(this.state);
        // console.log('current state'+this.state);
    }

    componentWillReceiveProps(nextProps) {
        debugger
        if (nextProps.receiptDetailData.obj !== undefined &&
            this.props.receiptDetailData.obj !== nextProps.receiptDetailData.obj) {
            this.generateData(nextProps.receiptDetailData.obj);
            // console.log(JSON.stringify(nextProps.receiptDetailData))
        }
    }


    generateData(response) {
        debugger
        var transactionArray = response["transactions"];
        this.setState({ responseData: response });
        // console.log(" generateData " + JSON.stringify(response))

        transactionArray = Utils.getTransactionArray(transactionArray);

        receiptdata = [];
        receiptInfoData = [];
        for (var index = 0; index < transactionArray.length; index++) {
            receiptdata.push({
                // 'key':index,
                'headerkey': 'id',
                'value': transactionArray[index].id,
            },
                {
                    // 'key':index,
                    'headerkey': 'receiptId',
                    'value': transactionArray[index].receiptId
                },
                {
                    // 'key':index,
                    'headerkey': 'Tx#',
                    'value': transactionArray[index].transactionId
                },
                // {
                //     // 'key':index,
                //     'headerkey': 'Step',
                //     'value': transactionArray[index].step
                // },
                {
                    // 'key':index,
                    'headerkey': 'Status',
                    'value': transactionArray[index].status
                },
                {
                    // 'key':index,
                    'headerkey': 'Date',
                    'value': transactionArray[index].transDate === undefined ? " - "
                        : moment(transactionArray[index].transDate).format('DD/MM/YYYY')
                }
            )
            receiptInfoData.push(receiptdata);
            receiptdata = [];
        }
        this.setState({
            step: response.step,
            loading: false,
            content: transactionArray,
            status: response.status,
            receiptDetailData: response,
        })
    }

    selectedRow = (record) => {
        if (record.headerkey === "Tx#") {
            this.props.history.push(pathConstants.TRANSACTION_DETAILS + '?txn=' + record.value);
        }
    }

    getTableData() {
        if (this.state.content.length < 1)
            return null;
        return (
            <div>
                <Tabs defaultActiveKey={"" + Utils.getIndex(this.state.step)} key={"" + Utils.getIndex(this.state.step)}>
                    {this.state.content.map((item, index) => (

                        <TabPane tab={Utils.getTitle(index)} key={index}>

                            <Table className={receiptInfoData[index].headerkey !== "Tx#" ? "" : "u_cursor_pointer"} {...this.state}
                                columns={APP_CONSTANTS.transInfoColumn}
                                dataSource={receiptInfoData[index]}
                                pagination={false}
                                onRow={(record) => ({ onClick: () => { this.selectedRow(record); } })}
                            />

                        </TabPane>

                    ))
                    }
                </Tabs><br /><br />
                {this.getJsonLd()}
                {this.getSubTabs()} 

            </div>
        )
    }

    getJsonLd() {
        return (
            <Collapse>
                <Panel header="jsonLd" key="1">
                    {
                        this.state.receiptDetailData && this.state.receiptDetailData.jsonLd ?
                            <ReactJson collapsed="2" src={JSON.parse(this.state.receiptDetailData.jsonLd)} />
                            : null
                    }
                </Panel>
            </Collapse>
        );
    }

    getCompressedGraph() {
        return (
            <div>
               {
                    this.state.receiptDetailData && this.state.receiptDetailData.jsonLd ?
                        <GraphContainerUnCompressed isFromReceipts={true}
                            nodeId={this.state.receiptDetailData}
                        />
                        :
                        null
                }
            </div>
        );
    }

    getGraph() {
        return (
            <div>
                {
                    this.state.receiptDetailData && this.state.receiptDetailData.jsonLd ?
                        <GraphContainer isFromReceipts={true}
                            nodeId={this.state.receiptDetailData}
                        />
                        :
                        null
                }
            </div>
        );
    }

    getSubTabs() {
        return (
            <Collapse>
                <Panel header="Graph" key="1">
                    <Tabs defaultActiveKey="1" >
                        <TabPane tab="Uncompressed Graph" key="1">
                             {this.getCompressedGraph()}
                        </TabPane>
                        <TabPane tab="Compressed Graph" key="2">
                            {this.getGraph()}
                        </TabPane>
                    </Tabs>
                </Panel>
            </Collapse>

        );
    }

    render() {
        console.log(this.state);
       
        return (
            <div>
                <BreadcrumbComponent breadcrumbData={[{ 'name': "block explorer" }, { 'name': "transactions" }, { 'name': "transaction details" }, { 'name': "receipt" }]} />
                <div className="receipts-page">
                    <Row>
                        {
                            this.state.loading ?
                                <Loader />
                                :
                                this.getTableData()
                        }
                    </Row>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        ...state,
        receiptDetailData: state.receipts.receiptDetailData,
    }
}
export default connect(
    mapStateToProps,
    {
        getReceiptData
    })(withRouter(Receipts));

