
import * as d3 from "d3";
import ReactDOM from 'react-dom'
import _ from 'lodash';


export var FORCE = (function (nsp) {

  var
    width = 1200,
    height = 800,
    color = d3.scaleOrdinal(d3.schemePaired),

    // .force("charge", d3.forceManyBody().strength(-200))
    // .force("link", d3.forceLink(links).distance(100))
    // .force("center", d3.forceCenter().x(nsp.width /2).y(nsp.height / 2))
    // .force("collide", d3.forceCollide([5]).iterations([5]));

    initForce = (nodes, links) => {
      debugger;
      d3.select("svg").remove();
      nsp.force = d3.forceSimulation(nodes)
        .force("charge", d3.forceManyBody())
        .force("link", d3.forceLink(links).distance(200))
        .force("center", d3.forceCenter().x(nsp.width /2 ).y(nsp.height/2))
        .force("collide", d3.forceCollide([5]).iterations([5]))
      ;
    },


    restartGraph = (nodes, links) => {
      d3.forceSimulation(nodes)
      .force("link", d3.forceLink(links).distance(200))
      .restart();
    },
    reDrawGraph = (node, links) =>{
      let _nodes = nsp.force.nodes();
      let _links = nsp.force.links();
      let _node = d3.select("svg").selectAll(".node");
      let _link = d3.select("svg").selectAll(".link");
      _link.data(links)
    },
    getColor = (d) => {
      return nsp.color(d.id);
    },

    enterNode = (selection) => {
      var circle = selection.select('circle')
        .attr("r", 10)
        .style("fill", getColor)
      //.style("stroke", "grey")
      //.style("stroke-width", "3px")

      selection.select('text')
        .attr("dy", 20)
        .style("fill", "black")
        .style("font-weight", "600")
        .style("text-transform", "uppercase")
        .style("text-anchor", "middle")
        .style("alignment-baseline", "middle")
        .style("font-size", "8px")
        .style("font-family", "inherit")
    },

    updateNode = (selection) => {
      selection
        .attr("transform", (d) => "translate(" + d.x + "," + d.y + ")")
       // .attr("cx", function (d) { return d.x; })
      //  .attr("cy", function (d) { return d.y; });
        .attr("cx", function(d) { return Math.max(30, Math.min(width - 30, d.x)); })
        .attr("cy", function(d) { return Math.max(30, Math.min(height - 30, d.y));}); 
    },

    enterLink = (selection) => {
      selection
        .attr("stroke-width", 1)
        .attr("stroke", "grey")
    },

    updateLink = (selection) => {
      
      selection
        .attr("x1", (d) => d.source.x)
        .attr("y1", (d) => d.source.y)
        .attr("x2", (d) => d.target.x)
        .attr("y2", (d) => d.target.y);
    },

    updateGraph = (selection) => {
      selection.selectAll('.node')
        .call(updateNode)
      selection.selectAll('.link')
        .call(updateLink);
    },

    dragStarted = (d) => {
      if (!d3.event.active) nsp.force.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y
    },

    dragging = (d) => {
      d.fx = d3.event.x;
      d.fy = d3.event.y
    },

    dragEnded = (d) => {
      if (!d3.event.active) nsp.force.alphaTarget(0);
      d.fx = null;
      d.fy = null
    },

    drag = () => d3.selectAll('g.node')
      .call(d3.drag()
        .on("start", dragStarted)
        .on("drag", dragging)
        .on("end", dragEnded)
      ),

    tick = (that) => {
      that.d3Graph = d3.select(ReactDOM.findDOMNode(that));
      nsp.force.on('tick', () => {
        that.d3Graph.call(updateGraph)
      });
    };


  nsp.width = width;
  nsp.color = color//d3.scale.category20();
  nsp.height = height;
  nsp.enterNode = enterNode;
  nsp.updateNode = updateNode;
  nsp.enterLink = enterLink;
  nsp.updateLink = updateLink;
  nsp.updateGraph = updateGraph;
  nsp.initForce = initForce;
  nsp.restartGraph = restartGraph;
  nsp.dragStarted = dragStarted;
  nsp.dragging = dragging;
  nsp.dragEnded = dragEnded;
  nsp.drag = drag;
  nsp.tick = tick;

  return nsp

})(FORCE || {})