import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col } from 'antd';
import './index.scss';
// constants
// import { pathConstants } from 'constants/path-constants';
import Loader from 'components/loader';
// import * as d3 from "d3";

import Link from './link'
import Node from './node'
// Actions
import { default as GraphActions } from "store/modelExplorer/actions";
const { getGraphNode, getGraphLink } = GraphActions;

// const APP_CONSTANTS = require('constants/app-constants');
const GRPAH_CONSTANTS = require('./variables')
var nodes = [];
var linkData = [];
var linkComponent = [];
var nodeComponent = [];
class KnowledgeGraph extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            graphnodes: [],
            graphlinkData: [],
            currrentNode: {},
        }
        this.onClickNode = this.onClickNode.bind(this);
        this.crawlJSON = this.crawlJSON.bind(this);
    }

    componentDidMount() {
        debugger;
        this.setState({ isLoading: true })
        if (this.props.isFromReceipts !== undefined && this.props.isFromReceipts) {
            this.generateDataFromJsonLd();
            return;
        }
        this.props.getGraphNode();
    }

    componentWillUnmount() {
        console.log("GRPAH COMPONENTWILLUNMOUNT")
        nodes = [];
        linkData = [];
        GRPAH_CONSTANTS.FORCE.initForce([], [])

        this.setState({
            graphnodes: [],
            graphlinkData: [],
            isLoading: false,
        })
    }

    generateDataFromJsonLd() {
        var node = [];
        var self = this;
        node = JSON.parse(this.props.nodeId.jsonLd);
        var nodeID = (JSON.parse(this.props.nodeId.jsonLd))['@id']
        this.pushNode(nodeID)
        this.crawlJSON(node, nodeID);
        linkComponent = [];
        nodeComponent = [];

        setTimeout(function () {
            self.generateNodesAndsLinksComponent();

            GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
            GRPAH_CONSTANTS.FORCE.tick(self)
            GRPAH_CONSTANTS.FORCE.drag()

            self.setState({
                graphnodes: nodes,
                isLoading: false,
            })
        }, 2000);
    }

    crawlJSON(json, parentNode) {
        console.log("CRAWLJSON JSON DATA " + JSON.stringify(json))
        console.log("CRAWLJSON JSON parentNode DATA " + JSON.stringify(parentNode))

        if (!json)
            return;
        this.pushNode(parentNode);
        let keys = Object.keys(json);
        for (let index = 0; index < keys.length; index++) {
            let key = keys[index];
            //|| key == "name"
            // if (key == "id" || key == "type")
            //     continue
            if (key === "id" || key === "type" || key === "name" || key === "@id" || key === "@name" || key === "@type" || key === "value")
                continue
            if (Array.isArray(json[key])) {
                this.crawlJSONArray(json[key], parentNode)
                continue;
            }
            this.pushNode(key)
            if (typeof json[key] === "object") {
                this.createLink(parentNode, key)
                this.crawlJSON(json[key], key)
                continue;
            }

            let value = String(json[key]);
            // var constructNodeData = {
            //     "name": value,
            //     "id": value
            // }
            this.createLink(parentNode, key)
            this.pushNode(value);
            this.createLink(key, value)
        }
    }
    crawlJSONArray(jsonArray, parentNode) {
        let keys = Object.keys(jsonArray);
        for (let index = 0; index < keys.length; index++) {
            let key = keys[index];
            if (Array.isArray(jsonArray[key])) {
                this.crawlJSONArray(jsonArray[key], parentNode)
                continue;
            }
            this.crawlJSON(jsonArray[key], parentNode)
        }
    }

    pushNode(nodeStr) {
        var nodeId;
        let isExists;
        // var nodes=this.state.graphnodes;
        let canExpand = true;
        if (typeof nodeStr === 'object') {
            nodeId = nodeStr.id;
            isExists = nodes.some(item => item.id === nodeId);
            canExpand = nodeStr.canFetchData ? nodeStr.canFetchData : false;
            nodeStr = nodeStr.name;
        } else {
            isExists = nodes.some(item => item.id === nodeStr);
            nodeId = nodeStr;
        }

        if (isExists)
            return

        nodes.push({ id: nodeId, name: nodeStr, canFetchData: canExpand })
        console.log("NODES AFTER ADDING " + JSON.stringify(nodes))

    }

    createLink(src, target) {
        if (typeof src === 'object') {
            let nodeId = src.id;
            let isExists = nodes.some(item => item.id === nodeId);
            if (!isExists) {
                this.pushNode(src);
            }
            src = nodeId;
        }
        if (typeof target === 'object') {
            let nodeId = target.id;
            let isExists = nodes.some(item => item.id === nodeId);
            if (!isExists) {
                this.pushNode(target);
            }
            target = nodeId;
        }
        if (src === target)
            return;
        let srcIndex = nodes.findIndex(item => item.id === src);
        let targetIndex = nodes.findIndex(item => item.id === target);
        console.log("SOURCE LINK INDEX " + srcIndex + "  DATA" + src)
        console.log("TARGET LINK INDEX " + targetIndex + "  DATA" + target)
        console.log("NODE DATA LOOP" + nodes.length)

        if (srcIndex === -1 || targetIndex === -1) {
            return;
        }
        console.log("SOURCE LINK BEFORE ADDING " + JSON.stringify(nodes[srcIndex]))
        console.log("TARGET LINK BEFORE ADDING " + JSON.stringify(nodes[targetIndex]))
        linkData.push({ source: srcIndex, target: targetIndex })
        console.log("LINKS AFTER ADDING " + JSON.stringify(linkData))
    }

    componentWillReceiveProps(nextProps) {
        debugger;
        console.log(nextProps);
        //&& nextProps.getGraphNode !== this.props.getGraphNode
        if (nextProps.GraphNode.content !== undefined &&
            this.props.GraphNode.content !== nextProps.GraphNode.content) {
            if (nextProps.GraphNode.status === 200) {
                var node = [];
                var self = this;
                node = nextProps.GraphNode.content.node;
                node.push({ id: "invoice", name: "invoice" });
                //this.pushNode("invoice")
                for (var i = 0; i < node.length; i++) {
                    this.pushNode(node[i])
                }

                for (var j = 0; j < node.length; j++) {
                    this.createLink("invoice", node[j])
                }

                linkComponent = [];
                nodeComponent = [];

                this.generateNodesAndsLinksComponent();

                GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
                GRPAH_CONSTANTS.FORCE.tick(this)
                GRPAH_CONSTANTS.FORCE.drag()

                this.setState({
                    graphnodes: nodes,
                    isLoading: false,
                })

            }
            console.log("NODES " + JSON.stringify(node));
        }


        if (nextProps.GraphLink.content !== undefined &&
            this.props.GraphLink.content !== nextProps.GraphLink.content) {
            self = this
            var Graphobject = nextProps.GraphLink.content.node[0];
            this.crawlJSON(Graphobject, this.state.currrentNode);
            debugger
            // const data = self.state;
            linkComponent = [];
            nodeComponent = [];
            setTimeout(function () {
                self.generateNodesAndsLinksComponent();
                //GRPAH_CONSTANTS.FORCE.restartGraph(nodes,linkData)
                GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
                GRPAH_CONSTANTS.FORCE.tick(self)
                GRPAH_CONSTANTS.FORCE.drag()
                self.setState(prevState => ({
                    //graphnodes: [...prevState.graphnodes, nodeCopy,],
                    isLoading: false,
                }));
                self.forceUpdate()
            }, 1000);
        }
    }

    generateNodesAndsLinksComponent() {
        nodeComponent = nodes.map((node) => {
            console.log("NODE DATA " + JSON.stringify(node))
            return (
                <Node
                    data={node}
                    name={node.name}
                    key={node.id}
                    nodeClicked={this.onClickNode}
                />);
        });

        linkComponent = linkData.map((link) => {
            console.log("NODE LINK " + JSON.stringify(link))
            return (
                <Link
                    key={link.key}
                    data={link}
                />);
        });
    }


    //graph event callbacks
    onClickNode(nodeId, name) {
        if (this.props.isFromReceipts !== undefined && this.props.isFromReceipts) {
            return;
        }
        var obj = {
            "name": name,
            "id": nodeId
        }
        this.setState({ currrentNode: obj, isLoading: true })
        console.log(`Clicked node ${nodeId}`);
        this.props.getGraphLink(nodeId);
    };

    getLinks() {
        console.log("getLinks " + JSON.stringify(linkComponent))
        return linkComponent;
    }

    getNodes() {
        console.log("getNodes " + JSON.stringify(nodeComponent))
        return nodeComponent;
    }

    render() {
        return (
            <div className="graph__container">
                <Row>
                    <Col span={24} >
                        {this.state.isLoading ?
                            <Loader />
                            : null
                        }

                        <svg
                            width={GRPAH_CONSTANTS.FORCE.width}
                            height={GRPAH_CONSTANTS.FORCE.height}
                        >
                            <g>
                                {this.getLinks()}
                            </g>
                            <g>
                                {this.getNodes()}
                            </g>
                        </svg>

                    </Col>
                </Row>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        ...state,
        GraphNode: state.modelExplorer.GraphNode,
        GraphLink: state.modelExplorer.GraphLink,
    }
}

export default connect(
    mapStateToProps,
    {
        getGraphNode,
        getGraphLink
    })(withRouter(KnowledgeGraph));

