import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import * as d3 from "d3";
const GRPAH_CONSTANTS = require('./variables')
class Link extends Component {

    componentDidMount() {
        this.d3Link = d3.select(ReactDOM.findDOMNode(this))
            .datum(this.props.data)
            .call(GRPAH_CONSTANTS.FORCE.enterLink);
    }

    componentDidUpdate() {
        this.d3Link.datum(this.props.data)
            .call(GRPAH_CONSTANTS.FORCE.updateLink)
    }

    render() {
        // console.log(this.props.data)
        return (
            this.props.data.type === 2 && !this.props.data.isParent?
                null
            :
                <line className='link' />
        );
    }
}

export default Link;
