
import * as d3 from "d3";
import ReactDOM from 'react-dom'
// import _ from 'lodash';


export var FORCE = (function (nsp) {

  var
    width = 1000,
    height = 1000,
    // width = window.innerWidth,
    // height = window.innerHeight,

    color = d3.scaleOrdinal(d3.schemePaired),
    // .force("charge", d3.forceManyBody().strength(-200))
    // .force("link", d3.forceLink(links).distance(100))
    // .force("center", d3.forceCenter().x(nsp.width /2).y(nsp.height / 2))
    // .force("collide", d3.forceCollide([5]).iterations([5]));

    initForce = (nodes, links) => {
      nsp.force = d3.forceSimulation(nodes)
        .force("charge", d3.forceManyBody())
        .force("center", d3.forceCenter().x(nsp.width /2 ).y(nsp.height / 2))
        .force("link", d3.forceLink(links).distance(100).id((d) => {
            // console.log('d links'+d.index)
            return d.index;
        } ))
        // .force("link", d3.forceLink(links).distance(200))
        .force("collide", d3.forceCollide([5]).iterations([5]))
        .restart()
        // .alphaDecay(0)
      //  .on("tick", tick);
       
    },


    stop = ()=> {
      nsp.force.stop();
      // selection.exit().remove();
      // link.exit().remove();
    },

    restartGraph = (nodes, links) => {
      d3.forceSimulation(nodes)
        .force("node",d3.forceSimulation(nodes))
        .force("link", d3.forceLink(links).distance(100))
      .restart();
    },

    getColor = (d) => {
      return nsp.color(d.id);
    },

    enterNode = (selection) => {
      var circle = selection.select('circle')
        .attr("r", 10)
        .style("fill", getColor)
        .call(d3.drag()
          .on("start", dragStarted)
          .on("drag", dragging)
          .on("end", dragEnded)
        )
        .on("click",nodeClick)
      //.style("stroke", "grey")
      //.style("stroke-width", "3px")

      selection.select('text')
        .attr("dy", 20)
        .style("fill", "black")
        .style("font-weight", "600")
        .style("text-transform", "uppercase")
        .style("text-anchor", "middle")
        .style("alignment-baseline", "middle")
        .style("font-size", "8px")
        .style("font-family", "cursive")


    },

    nodeClick = (d) => {
      console.log('click node'+ JSON.stringify(d));
     
    },

    updateNode = (selection) => {

      selection.call(d3.drag()
        .on("start", dragStarted)
        .on("drag", dragging)
        .on("end", dragEnded)
      )
       const a = Math.random();
       const b = Math.random();
      //  selection.data(nodes).exit().remove();
        selection
         .attr("transform", (d) => {
          
          if(!isNaN(d.x) && !isNaN(d.y)){
            return "translate(" + d.x + "," + d.y + ")"
          }
          return "translate(" + a + "," + b + ")"
         })
          .attr("cx", function(d) { 
            if(!isNaN(d.x)){
              return Math.max(30, Math.min(width - 30, d.x)); 
            }
            return a;
          })
          .attr("cy", function(d) { 
            if(!isNaN(d.y)){
              return Math.max(30, Math.min(width - 30, d.y)); 
            }
            return b;
          })

         
        // selection
        //   .attr("transform", (d) => "translate(" + d.x + "," + d.y + ")")
        //   .attr("cx", function(d) { return Math.max(30, Math.min(height - 30, d.x));})
        //   .attr("cy", function(d) { return Math.max(30, Math.min(height - 30, d.y));});

    },

    enterLink = (selection) => {
      selection
        .attr("stroke-width", 1)
        .attr("stroke", "grey")
    },

    updateLink = (selection) => {

      selection
      .attr("x1", (d) => 
      { 
        if(isNaN(d.source.x)){
          return Math.random();
        }
        return d.source.x;
      })
      .attr("y1", (d) => 
      { 
        if(isNaN(d.source.y)){
          return Math.random();
        }
        return d.source.y;
      })
      .attr("x2", (d) => 
      { 
        if(isNaN(d.target.x)){
          return Math.random();
        }
        return d.target.x;
      })
      .attr("y2", (d) => 
      { 
        if(isNaN(d.target.y)){
          return Math.random();
        }
        return d.target.y;
      })
     
      // selection
      //   .attr("x1", (d) => d.source.x)
      //   .attr("y1", (d) => d.source.y)
      //   .attr("x2", (d) => d.target.x)
        // .attr("y2", (d) => d.target.y);
    },

    updateGraph = (selection) => {
      selection.selectAll('.node')
        .call(updateNode)
      selection.selectAll('.link')
        .call(updateLink);

    },

    dragStarted = (d) => {
      if (!d3.event.active) nsp.force.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
     
    },

    dragging = (d) => {
      d.fx = d3.event.x;
      d.fy = d3.event.y
    },

    dragEnded = (d) => {
      if (!d3.event.active) nsp.force.alphaTarget(0);
      d.fx = null;
      d.fy = null;
      // d.fx = d3.event.x;
      // d.fy = d3.event.y;
    },

    drag = () => d3.selectAll('g.node')
      .call(d3.drag()
        .on("start", dragStarted)
        .on("drag", dragging)
        .on("end", dragEnded)
         
      ),

    

    tick = (that) => {
      that.d3Graph = d3.select(ReactDOM.findDOMNode(that));
      nsp.force.on('tick', () => {
        that.d3Graph.call(updateGraph)
      });
    };
   
    


  nsp.width = width;
  nsp.color = color//d3.scale.category20();
  nsp.height = height;
  nsp.enterNode = enterNode;
  nsp.updateNode = updateNode;
  nsp.enterLink = enterLink;
  nsp.updateLink = updateLink;
  nsp.updateGraph = updateGraph;
  nsp.initForce = initForce;
  nsp.stop = stop;
  nsp.restartGraph = restartGraph;
  nsp.dragStarted = dragStarted;
  nsp.dragging = dragging;
  nsp.dragEnded = dragEnded;
  nsp.drag = drag;
  nsp.tick = tick;
  nsp.nodeClick =  nodeClick;
  // nsp.start = start;
  

  return nsp

})(FORCE || {})