import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
// import ReactDOM from 'react-dom'
// import { Row } from 'antd';
// constants
// import { pathConstants } from 'constants/path-constants';
import Loader from 'components/loader';

// import * as d3 from "d3";

import Link from './link'
import Node from './node'
// Actions
import { default as GraphActions } from "store/modelExplorer/actions";
const { getGraphNode, getGraphLink } = GraphActions;

// const APP_CONSTANTS = require('constants/app-constants');
const GRPAH_CONSTANTS = require('./variables');

var nodes = [];
var nodeCopy = [];
var linkData = [];
var linkDataCopy = [];
var linkComponent = [];
var nodeComponent = [];

class KnowledgeGraphUnCompressed extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            showHideFlag:0,
            graphnodes: [],
            graphlinkData: [],
            currrentNode: {},
        }
        this.onClickNode = this.onClickNode.bind(this);
        this.crawlJSON = this.crawlJSON.bind(this);
    }

    componentDidMount() {
        debugger;
        this.setState({ isLoading: true })
        if (this.props.isFromReceipts !== undefined && this.props.isFromReceipts) {
            // var nodeID = (JSON.parse(this.props.nodeId.jsonLd))['@id']
            // //console.log("KnowledgeGraph NODEID " + nodeID);
            // this.props.getGraphNode(nodeID);
            this.generateDataFromJsonLd();
            return;
        }
        this.props.getGraphNode();
    }

    componentWillUnmount() {
        //console.log("GRPAH COMPONENTWILLUNMOUNT")
        nodes = [];
        linkData = [];
        linkComponent = [];
        nodeComponent = [];
        this.setState({
            graphnodes: [],
            graphlinkData: [],
            isLoading: false,
        })
    }

    generateDataFromJsonLd() {
        var node = [];
        var self = this;
        node = JSON.parse(this.props.nodeId.jsonLd);
        var nodeID = (JSON.parse(this.props.nodeId.jsonLd))['@id']
        this.pushNode(nodeID)
        //node.push({ id: nodeID, name: nodeID });
        this.crawlJSON(node, nodeID);
        linkComponent = [];
        nodeComponent = [];
        setTimeout(function () {
            self.generateNodesAndLinksComponent();
            GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
            GRPAH_CONSTANTS.FORCE.tick(self)
            GRPAH_CONSTANTS.FORCE.drag()
            self.setState(({
                graphnodes: nodes,
                isLoading: false,
            }));
        }, 2000);
    }

    crawlJSON(json, parentNode,flag) {
        // console.log("CRAWLJSON JSON DATA " + JSON.stringify(json))
        // console.log("CRAWLJSON JSON parentNode DATA " + JSON.stringify(parentNode))
        let type;
        let parentName = parentNode.name;
        debugger;
        if(flag === 0){
            type = 1;
        }else{
            type = 2;
        }

        debugger
        if (!json)
            return;

        let keys = Object.keys(json);
        for (let index = 0; index < keys.length; index++) {
            let key = keys[index];
            //|| key == "name"
            // if (key == "id" || key == "type")
            //     continue
            if (key === "id" || key === "type" || key === "name" || key === "@id" || key === "@name" || key === "@type" || key === "value")
                continue
            if(Array.isArray(json[key])) {
                this.crawlJSONArray(json[key], parentNode,flag)
                continue;
            }
            // debugger
            // console.log('------'+key,parentName);
            let id  = key + "_"+ parentName;
            
            var obj = {
                'name':key,
                'id':id
            }
            this.pushNode(obj,type,false);
            if (typeof json[key] === "object") {
                //TODO Create link parentNode to Key
                // debugger
                this.createLink(parentNode,id,type,false)
                this.crawlJSON(json[key],key,flag)
                continue;
            }

            // debugger
            let value = String(json[key]);
            var constructNodeData = {
                "name": value,
                "id": value +"_"+parentName,
               
            }
           
            // debugger
            this.createLink(parentNode, id,type,false)
            this.pushNode(constructNodeData,type,false);
            // debugger
            this.createLink(id, constructNodeData,type,false)
        }
    }

    crawlJSONArray(jsonArray, parentNode,flag) {
        // console.log(jsonArray);
        let keys = Object.keys(jsonArray);
        for (let index = 0; index < keys.length; index++) {
            let key = keys[index];
            // debugger
            if (Array.isArray(jsonArray[key])) {
                this.crawlJSONArray(jsonArray[key], parentNode,flag)
                continue;
            }
            this.crawlJSON(jsonArray[key], parentNode,flag)
        }
    }

    pushNode(nodeStr,type,parent) {
        debugger;
        // console.log(type,parent);
        var nodeId;
        let isExists;
        var type = type !== undefined?type:0;
        var parent = parent !== undefined?parent:false;
       
        // var nodes=this.state.graphnodes;
        let canExpand = true;
        if (typeof nodeStr === 'object') {
            nodeId = nodeStr.id;
            isExists = nodes.some(item => item.id === nodeId);
            canExpand = nodeStr.canFetchData ? nodeStr.canFetchData : false;
            nodeStr = nodeStr.name;
        } else {
            isExists = nodes.some(item => item.id === nodeStr);
            nodeId = nodeStr;
        }

        // console.log(isExists,nodeStr);
        if (isExists){
            for(var i in nodes){
                if(nodes[i].id === nodeId){
                        nodes[i].type = type;
                }
            }
            return
        }

        // console.log("NODES ADDING DATA  ID- " + nodeId + " NAME - " + nodeStr)
        nodes.push({ id: nodeId, name: nodeStr, canFetchData: canExpand, type:type, isParent:parent })
        nodeCopy.push({ id: nodeId, name: nodeStr, canFetchData: canExpand, type:type, isParent:parent })
        // console.log("NODES AFTER ADDING " + JSON.stringify(nodes))
    }

    createLink(src, target,type,parent) {
        debugger;
        // console.log('source'+src);
        // console.log('target'+JSON.stringify(target));
        let isSrcExists,isTargetExists;
        if (typeof src === 'object') {
            let nodeId = src.id;

            let isExists = nodes.some(item => item.id === nodeId);
            if (!isExists) {
                this.pushNode(src,type,parent);
            }
            src = nodeId;

            // if link sc already exists
            isSrcExists = linkData.some(item => item.source.id === nodeId);
            
        }
        if (typeof target === 'object') {
            let nodeId = target.id;
            let isExists = nodes.some(item => item.id === nodeId);
            if (!isExists) {
                this.pushNode(target,type,parent);
            }
            target = nodeId;
            isTargetExists = linkData.some(item => item.target.id === nodeId);
           
        }
        if (src === target)
            return;
        let srcIndex = nodes.findIndex(item => item.name === src);
        if (srcIndex === -1)
            srcIndex = nodes.findIndex(item => item.id === src);
        let targetIndex = nodes.findIndex(item => item.id === target);
        if (targetIndex === -1)
            targetIndex = nodes.findIndex(item => item.name === target);
        if (srcIndex === -1 || targetIndex === -1) {
            return;
        }
        //console.log("SOURCE LINK BEFORE ADDING " + JSON.stringify(nodes[srcIndex]))
        //console.log("TARGET LINK BEFORE ADDING " + JSON.stringify(nodes[targetIndex]))
        
        if(!isSrcExists && !isTargetExists){
            linkData.push({ source: srcIndex, target: targetIndex, type:type, isParent:parent })
            linkDataCopy.push({ source: srcIndex, target: targetIndex, type:type, isParent:parent})
            //console.log("LINKS AFTER ADDING " + JSON.stringify(linkData)) 
                
        }else{
            for(var i in linkData){
                if(src === linkData[i].source.id && target === linkData[i].target.id){
                    linkData[i].type = type;
                }
            }
        }
      
    }

    componentWillReceiveProps(nextProps) {
        debugger;
        //console.log(nextProps);
        //&& nextProps.getGraphNode !== this.props.getGraphNode
        if (nextProps.GraphNode.content !== undefined &&
            this.props.GraphNode.content !== nextProps.GraphNode.content) {
            if (nextProps.GraphNode.status === 200) {
                var node = [];
                var self = this;
                node = nextProps.GraphNode.content.node;
                node.push({ id: "invoice", name: "invoice" });
                //this.pushNode("invoice")
                for (var i = 0; i < node.length; i++) {
                    this.pushNode(node[i],0,true)
                }

                for (var j = 0; j < node.length; j++) {
                    this.createLink("invoice", node[j],0,true)
                }

                this.generateNodesAndLinksComponent();

               
                GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
                GRPAH_CONSTANTS.FORCE.tick(this)
                GRPAH_CONSTANTS.FORCE.drag()
              

                this.setState({
                    graphnodes: node,
                    isLoading: false,
                })
            }
            //console.log("NODES " + JSON.stringify(node));
        }


        if (nextProps.GraphLink.content !== undefined &&
            this.props.GraphLink.content !== nextProps.GraphLink.content) {
            self = this
            var Graphobject = nextProps.GraphLink.content.node[0];
            nodeCopy = [];
            linkDataCopy = [];
            this.crawlJSON(Graphobject,this.state.currrentNode,this.state.showHideFlag);
            linkComponent = [];
            nodeComponent = [];
            // self.generateNodesAndLinksComponent();

            setTimeout(function () {
               
                self.generateNodesAndLinksComponent();
                // GRPAH_CONSTANTS.FORCE.restartGraph(nodes,linkData)
                GRPAH_CONSTANTS.FORCE.initForce(nodes, linkData)
                GRPAH_CONSTANTS.FORCE.tick(self)
                GRPAH_CONSTANTS.FORCE.drag()
                self.setState(prevState => ({
                    //graphnodes: [...prevState.graphnodes, nodeCopy,],
                    isLoading: false,
                }));
                self.forceUpdate()
            }, 1000);

            // setTimeout(function () {
            //     self.setState(prevState => ({
            //         isLoading: false,
            //     }));
            // },7000)

        }
    }

 


    //graph event callbacks
    onClickNode(nodeId, name,type,parent,flag) {
        // console.log('clicked Node id'+ nodeId, name);
        if (this.props.isFromReceipts !== undefined && this.props.isFromReceipts) {
            return;
        }
        var obj = {
            "name": name,
            "id": nodeId,
            // "type":type,
        }
        
        //console.log(`Clicked node ${nodeId}`);
        if(!isNaN(name) && parent && nodeId !== "invoice"){

            for(var i in nodes){
                if(nodes[i].id === nodeId){
                    nodes[i].type = type;
                    console.log('node id'+JSON.stringify(nodes[i]))
                    // return;
                }
                
            }

            // if(type !== 2){
                this.setState({ currrentNode: obj, isLoading: true,showHideFlag:flag })
                GRPAH_CONSTANTS.FORCE.stop();
                this.props.getGraphLink(nodeId);
            // }
        }
    };

    generateNodesAndLinksComponent() {

        nodeComponent = nodes.map((node,index) => {
            //console.log("NODE DATA " + JSON.stringify(node))
            return (
                <Node
                    data={node}
                    name={node.name}
                    key={index}
                    nodeClicked={this.onClickNode}
                />);
        });

        linkComponent = linkData.map((link,index) => {
            //console.log("NODE LINK " + JSON.stringify(link))
            return (
                <Link
                    key={index}
                    data={link}
                />);
        });
    }

    getLinks() {
        //console.log("getLinks " + JSON.stringify(linkComponent))
        return linkComponent;
    }

    getNodes() {
        //console.log("getNodes " + JSON.stringify(nodeComponent))
        return nodeComponent;
    }

    render() {
        return (
            <div className="graph__container">
                {this.state.isLoading ?
                    <Loader />
                    : null
                }
                {/* <div className="chart"></div> */}
                <svg className="d3"
                    width={GRPAH_CONSTANTS.FORCE.width}
                    height={GRPAH_CONSTANTS.FORCE.height}
                >
                    <g>
                        {this.getLinks()}
                    </g>
                    <g>
                        {this.getNodes()}
                    </g>
                </svg>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        ...state,
        GraphNode: state.modelExplorer.GraphNode,
        GraphLink: state.modelExplorer.GraphLink,
    }
}

export default connect(
    mapStateToProps,
    {
        getGraphNode,
        getGraphLink
    })(withRouter(KnowledgeGraphUnCompressed));

